<template>
  <div>
    <div class="wrap">
      <div class="header">
        <i class="iconfont icon-back" @click="handleBack"></i>
        <ul class="nav">
          <li class="nav-item" :class="{active: showFavorite}" @click="handleFavorite">我的收藏</li>
          <li class="nav-item" :class="{active: !showFavorite}" @click="handleHistory">最近播放</li>
        </ul>
      </div>
      <div class="content">
        <music-list :list=favorite v-if="showFavorite"></music-list>
        <music-list :list=playHistory v-if="!showFavorite"></music-list>
      </div>
    </div>
  </div>
</template>

<script>
import MusicList from 'components/MusicList'
import { mapGetters } from 'vuex'

export default {
  name: 'user',
  components: {
    MusicList
  },
  data () {
    return {
      showFavorite: true
    }
  },
  computed: {
    ...mapGetters([
      'favorite',
      'playHistory'
    ])
  },
  methods: {
    handleBack () {
      this.$router.back()
    },
    handleFavorite () {
      this.showFavorite = true
    },
    handleHistory () {
      this.showFavorite = false
    }
  }
}
</script>

